import React, { useState } from 'react'
import { Grid, Typography, IconButton } from '@material-ui/core'
import ArrowDropDown from "@material-ui/icons/ArrowDropDown"
import ServiziOffertiDetail from './ServiziOffertiDetail'

const Servizio = (props) => {

    const [displayMore, setDisplayMore] = useState(false)

const clickHandler = ()=> {
    console.log(displayMore)
    setDisplayMore(!displayMore)
}

    return (
        
        <Grid item xs={12}
        container
        display="flex"
        alignItems="center"
        justifyContent="flex-start"
        spacing={2}
        >
            <Grid container item xs={6}  alignItems='center' justifyContent='flex-start' spacing={2} >
            
        <Grid item xs={6} >
            {props.icon}
        </Grid>
         
        <Grid item xs={6} >
            <Typography variant={'h6'}>{props.typography}</Typography>
        </Grid>
            </Grid>

        <Grid container item  xs={6}>
        <Grid item xs={6}></Grid>
        <Grid item xs={6}>

            <IconButton onClick={clickHandler}>
            <ArrowDropDown/>
            </IconButton>

        </Grid>
        </Grid>

        <Grid item xs={12}>
            {displayMore && <ServiziOffertiDetail/>}
       
        </Grid>

      </Grid>
    )
}

export default Servizio
