import React from 'react'
import { Formik, Form, Field, ErrorMessage} from 'formik';
import {Grid  , Typography, Box,  TextField , Button, ButtonGroup , CardContent, Card, CardMedia, LinearProgress } from "@material-ui/core";

import phone from "../../assets/phone.svg";
import useStyles from '../Utils/Styles';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import * as Yup from 'yup'

import emailjs from 'emailjs-com'

const SERVICE_ID = process.env.REACT_APP_SERVICE_ID
const TEMPLATE_ID = process.env.REACT_APP_TEMPLATE_ID
const USER_ID = process.env.REACT_APP_EMAILJS_ID






 const Contacts = () => {

  
  const sendEmailHandler = (object)=> {
 
    emailjs.send(SERVICE_ID,TEMPLATE_ID, object, USER_ID).then(result=> {
      console.log(result.text);
      toast.success('Messaggio inviato con successo')
     
    }, error=> {
      toast.error("Si e' verificato un errore!" )
    
      console.log(error);
    })
  }


  const classes = useStyles()
const validationSchema = Yup.object({
  firstName: Yup.string().required('Campo obbligatorio'),
  lastName: Yup.string().required('Campo obbligatorio'),
  email: Yup.string().email('Formato mail non valido').required('Campo obbligatorio'),
  message:Yup.string().max(250, 'Max caratteri 250').required('Campo obbligatorio')
})

   const initialValues = {
    firstName: '',
    lastName: '',
    email:'',
    message: '',
   }

   return (

     <Card>
     
      
       <CardContent >
       <ToastContainer position="top-right" autoClose={5000} hideProgressBar={false} newestOnTop={false}/>
       
         <Grid container direction='column' spacing={2} alignContent='center'>

<Grid item xs={12}> 

<Box mb={2}>
        <Typography style={{textAlign:'center'}} variant='h4'>Contattami</Typography>
         </Box>

         <Formik onSubmit={(values,actions)=> {
           
             sendEmailHandler(values)
             actions.setSubmitting(false)
             actions.resetForm()
             
           
         }} validateOnMount initialValues={initialValues} validationSchema={validationSchema} >
           
       {formik=> {
         console.log(formik)
         return (
           <>
             {formik.isSubmitting && <Box m={2}><LinearProgress/></Box> }
             {/* {formik.isSubmitting && <h1>{formik}</h1>} */}
           <Form>
     
          <Grid container  direction='column' alignItems='center' spacing={2} >
          <Grid item >
 
       
        <Field
          as={TextField}
          id="firstName"
          name="firstName"
          type="text"
          label = 'Nome'
          variant='outlined'> 
          </Field>
          <ErrorMessage name='firstName'>
            {errorMsg=> <div style={{color:'red'}}>{errorMsg}</div>}
          </ErrorMessage>
          </Grid>
 
          <Grid item>
        <Field
          as={TextField}
          id="lastName"
          name="lastName"
          type="text"
          label='Cognome'
          variant='outlined'/>
        <ErrorMessage name='lastName'>
            {errorMsg=> <div style={{color:'red'}}>{errorMsg}</div>}
          </ErrorMessage>
          </Grid>
 
 
          <Grid item>
        
        <Field
          as={TextField}
          id="email"
          name="email"
          type="email"
          label='Email'
          variant='outlined'/>
 
            <ErrorMessage name='email'>
            {errorMsg=> <div style={{color:'red'}}>{errorMsg}</div>}
          </ErrorMessage>
          </Grid>
        
 
          <Grid item>
         
        <Field
          as={TextField}
          id="message"
          name="message"
          type="text"
          label='Messaggio'
          multiline={true}
          rows={6}
          variant='outlined'
          placeholder='Descrivi brevemente il tuo problema...'
          />
           <ErrorMessage name='message'>
            {errorMsg=> <div style={{color:'red'}}>{errorMsg}</div>}
          </ErrorMessage>
       </Grid>
 
        <Grid item xs={12}>
        <Button disabled={formik.isSubmitting}  type="submit" variant='contained' color='primary'>Submit</Button>
        </Grid>
        </Grid>
      </Form>
      </>
         )
       }
       }
    
     </Formik>
</Grid>

<Grid item xs={12}>
<Box mt={2}>
       

       <ButtonGroup>
        
       <Button variant="contained" href="tel:+39091520120"
      className={classes.button}
      startIcon={<CardMedia component='img' className={classes.iconSvg} src={phone}/> } >Chiama 091520120</Button>
   
       <Button variant="contained" href="tel:+393512530433"
      className={classes.button}
      startIcon={<CardMedia component='img' className={classes.iconSvg} src={phone}/> } >Chiama 3512530433</Button>
    
       </ButtonGroup>
      
    </Box>
  
</Grid>

         </Grid>
         
     
       </CardContent>
     </Card>
     
   );
 };

export default Contacts




