import React from "react";
import phone from "../../assets/phone.svg";
import {
  AppBar,Box,Toolbar,
  Typography,
  Button,
  CardMedia,  MenuList, MenuItem, Grid, Hidden, Menu
} from "@material-ui/core";


import MoreHorizIcon from '@material-ui/icons/MoreHoriz';
import IconButton from '@material-ui/core/IconButton';
import {Link} from 'react-scroll'
import useStyles from "../Utils/Styles";




const Header = () => {
  const classes = useStyles()

  const [anchorEl, setAnchorEl] = React.useState(null);



  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };




 
  return (
  <AppBar  position="static">
    <Toolbar >
      
      <Grid  container justifyContent='space-between' alignItems='center' alignContent='center' spacing={2} >
    <Hidden xsDown>
            <Grid item xs={4} container justifyContent='center' alignContent='center'>
                <Typography className={classes.title  } variant="h4" component='h1'  >
                     Dottoressa Marina Tricoli
                 </Typography>
            </Grid>
    </Hidden>

      <Hidden smUp>
        <Grid item xs={4} container justifyContent='center' alignContent='center'>
            <Typography className={classes.title  } variant="h6" component='h1'  >
                Dr.ssa Marina Tricoli
            </Typography>
        </Grid>
      </Hidden>

  <Hidden xsDown>
    <Grid item xs={4} container justifyContent='center' alignContent='center' >
      <Box m={1}>
    <Button  variant="contained" href="tel:+393512530433"
      className={classes.button}
      startIcon={<CardMedia component='img' className={classes.iconSvg} src={phone}/> } >Chiama</Button>
      </Box>
    </Grid>
  </Hidden>

    <Hidden smUp>
  <Grid item xs={4} container justifyContent='center' alignContent='center' >
      <Box m={1}>
    <Button size='small' variant="contained" href="tel:+393512530433"
      className={classes.button}
       ><CardMedia  component='img' className={classes.iconSvg} src={phone}/></Button>
      </Box>
    </Grid>
    </Hidden>

<Hidden smDown>
<Grid item xs={12} >
    <MenuList className={classes.menu}
      >       
      <Link
      to='servizi-offerti' smooth={true} duration={1000}
      >
      
        <MenuItem className={classes.menuItem} onClick={handleClose}>Servizi Offerti</MenuItem>
      </Link> 

      <Link to='curriculum' smooth={true} duration={1000} >
        <MenuItem className={classes.menuItem} onClick={handleClose}>Il mio curriculum</MenuItem>
      
      </Link>
      <Link to='domande-frequenti' smooth={true} duration={1000}>
      
        <MenuItem className={classes.menuItem} onClick={handleClose}>Domande Frequenti</MenuItem>
      </Link>
      <Link to='contattami' smooth={true} duration={1000}>
        <MenuItem className={classes.menuItem} onClick={handleClose}>Contattami</MenuItem>
      
      </Link>
      <Link to='dove-trovarmi' smooth={true} duration={1000}>
        <MenuItem className={classes.menuItem} onClick={handleClose}>Dove trovarmi</MenuItem>
      
      </Link>
      </MenuList>
    </Grid>
</Hidden>

<Hidden mdUp >
<Grid item xs={4} container justifyContent='center' alignContent='center'>
<IconButton  aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
       <MoreHorizIcon />
      </IconButton>
      <Menu id="simple-menu"
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
        className={classes.menu}>

<Link
      to='servizi-offerti' smooth={true} duration={1000}
      >
      
        <MenuItem onClick={handleClose}>Servizi Offerti</MenuItem>
      </Link> 

      <Link to='curriculum' smooth={true} duration={1000} >
        <MenuItem onClick={handleClose}>Il mio curriculum</MenuItem>
      
      </Link>
      <Link to='domande-frequenti' smooth={true} duration={1000}>
      
        <MenuItem onClick={handleClose}>Domande Frequenti</MenuItem>
      </Link>
      <Link to='contattami' smooth={true} duration={1000}>
        <MenuItem onClick={handleClose}>Contattami</MenuItem>
      
      </Link>
      <Link to='dove-trovarmi' smooth={true} duration={1000}>
        <MenuItem onClick={handleClose}>Dove trovarmi</MenuItem>
      
      </Link>
      </Menu>
    </Grid>
</Hidden>

  
      </Grid>


    </Toolbar>
  </AppBar>

  );
};

export default Header;
