import React from 'react'
import { useTheme,Typography, Box, CardContent, Card, Grid, CardMedia} from "@material-ui/core";
import Domanda from './Domanda';
import DOMANDE from './Domande';
import questionMarkIcon from '../../assets/mono-question-mark.svg'

const DomandeFrequenti = () => {
    const theme = useTheme()
    return (
            <Card  style={{backgroundColor:theme.palette.primary.main}}>
        <CardContent>
            <Grid alignItems='center' display='flex' justifyContent='center' container>

            <Grid item xs={12} container>

                <Grid item xs={1}></Grid>

        <Grid xs={3} item>
        <CardMedia image={questionMarkIcon} component='img' style={{height:100, width:100}}  alt='A black question mark icon'  />
        </Grid>
        <Grid item xs={6}>

        <Box p={2}>
            <Typography align='center' variant='h4'>
           Cosa chiedere al tuo medico
           </Typography>
            </Box>
        </Grid>
        <Grid item xs={1}></Grid>
            </Grid>

            <Grid item xs={12}>
            <Box p={2}>
           <Typography variant='body1'>
               Lorem ipsum dolor sit amet consectetur adipisicing elit. Laudantium sequi debitis maxime! Minima praesentium itaque eligendi aliquam, hic dolorem officiis tempora delectus harum? Reprehenderit illo iure tempore sunt voluptates soluta?
           </Typography>
            </Box>

            </Grid>
            </Grid>

        {DOMANDE.map((dom)=> {
            
           return <Domanda key={dom.title} title={dom.title} answer={dom.answer}/>
        })}
           
        </CardContent>
            </Card>
       
    )
}

export default DomandeFrequenti
